<script>
	export default {
		data() {
			return {
				reviewClassList:[
					{
						classId:'111',
						title:'3434',
						bannerImg:'hdjshdjs',
						jobTitle:'jjjj',
						label:'7878'
					}
				] // 咨询师列表
			}
		},
		onLoad(option) {
			//初始化数据
			this.loadData();
		},
		methods: {
			loadData(pullRefresh) {
				let that = this
				//查询专家列表
				this.$apis.postPsychoMetrics().then(res => {
					if (res.code == 200) {
						res.result.forEach((row) => {
							row.bannerImg = that.$config.aliYunEndpoint + row.bannerImg
							that.comList.push(row)
						})
					} else {
						uni.showToast({
							title: res.msg
						})
					}
				}).catch(err => {
					console.log(err)
				})
				//查询分类
				this.$apis.postReviewClass().then(res => {
					if (res.code == 200) {
						res.result.forEach((row) => {
							that.reviewClassList.push(row)
						})
					}
				}).catch(err => {
					console.log(err)
				})
			},
			screenList(){},
			// 亲子关系
			relationshipClick(){},
			// 人际关系
			interpersonalClick(){},
			// 评测解读
			interpretationClick(){},
			search(){},
			clear(){},
			// 立即预约
			reservationClick(){}
		}
	}
</script>

